import React, { useState } from "react";
import Logo from "../../assets/icon.jpg";
import css from "./Header.module.css";
import close from "../../assets/close.png";
import { Link, useNavigate  } from "react-router-dom";
import axios from "axios";

const Header = () => {
  const [showMenu, setShowMenu] = useState(false);
  const [showLoginMenu, setshowLoginMenu] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

  const toggleBars = () => {
    setShowMenu((showMenu) => !showMenu);
  };

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post("/", {
        username: username,
        password: password,
      });

      // Handle the response as needed
      console.log(response.data);

      // Check if login was successful
      if (response.data.message === "Login successful") {
        // Redirect to another page
        window.location.href = "/dashboard";
      } else {
        // Handle login error
        setError("Login failed. Please try again.");
      }
    } catch (error) {
      setError("An error occurred. Please try again.");
      console.log(error);
    }
  };

  return (
    <div className={css.container} >
      <div className={css.logo}>
        
        <Link to="/">
          <img 
            src={Logo} 
            alt="home" 
            style={{borderRadius:'50px',height:'2.8rem',width:'2.8rem'}}
          />
        </Link>
        <span>Polytechnic</span>
      </div>

      <div className={css.right}>
        <div className={css.bars} onClick={toggleBars}>
          <i class="fa-solid fa-bars"></i>
        </div>

        <ul className={css.menu} style={{ display: showMenu ? "inherit" : "" }}>
          <li>
            <Link to="/news"> News</Link>
          </li>
          <li><Link to="/roadmap">Roadmap </Link></li>
          <li><Link to="/about-us">About us </Link></li>
          <li><Link to="/contact">Contact Us</Link></li>
          <li
            
          >
          </li>
        </ul>
            <a href="#" className={css.Login} onClick={() => {
              setshowLoginMenu((showLoginMenu) => !showLoginMenu);
            }}>
              Login
            </a>

        <div
          className={css.loginCover}
          style={{ display: showLoginMenu ? "block" : "none" }}
        >
          <img
            src={close}
            className={css.close}
            onClick={() => {
              setshowLoginMenu((showLoginMenu) => !showLoginMenu);
            }}
          />
          <h1>Login</h1>
          <div className={css.loginContainer}>
            <input
              type="text"
              placeholder="enter your username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              id="username"
              name="username"
              required
            />
            <input
              type="password"
              placeholder="enter your password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              id="password"
              name="password"
              required
            />
            <button type="submit" className={css.loginBtn} onClick={handleLogin}>
              Login
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
